@media (max-width: 1200px) {
    .container {
        max-width: 100%;
    }
    .languages {
        &__wrapper {
            width: 98%;
        }
    }
    .logo {
        padding: 19px 122px 84px 75px;
    }
    .top {
        &__menu {
            margin-right: 105px;
        }
        &__mobiles {
            width: 27%;
            height: 13%;
            margin: -150px 0 0 724px;
        }
    }
    .btn {
        margin-left: 322px;
    }

    .withprengi {
        &__arrow {
            display: block;
            margin: 0 auto;
        }
    }
    .middle {
        &__menu {
            width: 84%;
        }
        &__notebook {
            margin: 21px auto;
        }   
    }
    .advantages {
        &__wrapper {
            display: block;
            margin-left: 174px;
        }
        &__item {
            margin-bottom: 20px;
        }
        &__descr {
            font-size: 18px;
            margin: -200px 0 0 60px;
            width: 43%;
        }
    }
    .variants {
        &__wrapper {
            width: 100%;
        }
        &__picture {
            width: 599px;
        }
        &__header {
            margin: 22px 0 0 308px;
        }
        &__subheader {
            &_left {
                margin: -215px 0 0 200px;
            }
            &_right {
                margin: -217px 0 0 52px;
            }
        }
        &__descr {
            &_left {
                margin: -169px 0 0 127px;
                font-size: 16px;
            }
            &_right {
                margin: -168px 0 0 53px;
                font-size: 16px;
            }
        }
    }
    .start {
        &__picture {
            margin: 80px auto;
            display: block;
        }
        &__btn {
            display: block;
            margin: 30px auto;
        }
    }
    .footer {
        height: 100%;
        &__wrapper {
            column-gap: 43px;
            margin: 80px 0 0 0;
        }
    }
    hr {
        width: 960px;
    }
    
}

