.withprengi {
    &__questions__wrapper {
        display: flex;
        justify-content: center;
    }
    &__questions__item {
        padding: 75px 0 0 23px;
        
    }
    &__descr {
        width: 310px;
        margin: -50px 0 0 28px;
        font-size: 18px;
        font-weight: 300;
        font-stretch: normal;
        font-style: normal;
        line-height: 1.44;
        letter-spacing: normal;
        text-align: center;
        color: #393939;
        strong {
            font-weight: bold;
        }
    }
    &__arrow {
        width: 10%;
        margin-left: 703px;
    }
    &__header {
        margin: 0 33px 37px 63px;
        font-family: Roboto;
        font-size: 48px;
        font-weight: bold;
        font-stretch: normal;
        font-style: normal;
        letter-spacing: normal;
        text-align: center;
        color: #393939;
        text-transform: uppercase;
    }
    &__answers__item {
        width: 361px;
        margin-left: 23px;
    }
    
}