.container {
    width: 1520px;
}
.languages {
    margin-top: 10px;
    &__wrapper {
        display: flex;
        justify-content: space-around;
        img {
            float: left;
            margin-right: 14px;
        }
    }
    &__tel {
        font-size: 12px;
        font-weight: 300;
        font-stretch: normal;
        font-style: normal;
        letter-spacing: normal;
        text-align: center;
        color: #242424;
    }
    &__email {
        font-size: 12px;
        text-align: center;
    }
    &__address {
        font-size: 12px;
        text-align: center;
    }
}