.top {
    margin-top: 10px;
    background: url('../img/bg/bg_top.jpg') center (center/cover) no-repeat;
    height: 700px;
    &__menu {
        margin-top: -110px;
        text-align: right;
        &_link {
            margin: 25px 1px 56px 42px;
            font-size: 13px;
            font-stretch: normal;
            font-style: normal;
            line-height: 2;
            letter-spacing: normal;
            color: #ffffff;
            text-transform: uppercase;
            &:hover {
                color:#00c2f0;
                transition: 0.5s all;
                text-decoration: none;
            }
        }
    }
    &__header {
        margin: 133px 13px 27px 247px;
        font-size: 42px;
        font-weight: 900;
        font-stretch: normal;
        font-style: normal;
        line-height: 1.14;
        letter-spacing: normal;
        text-align: left;
        color: #ffffff;
    }
    &__subheader {
        margin: 27px 92px 39px 247px;
        font-size: 24px;
        font-weight: 300;
        font-stretch: normal;
        font-style: normal;
        line-height: 1.25;
        letter-spacing: normal;
        text-align: left;
        color: #ffffff;
    }
    &__mobiles {
        width: 434px;
        height: 569px;
        margin: -307px 0 0 512px;
    }
}
.logo {
    padding: 19px 122px 84px 251px;
}

