.modal {
    display: none;
    position: fixed;
    z-index: 1;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgba(0,0,0, 0.8);
    &__content {
        background-color: #fff;
        margin: 15% auto;
        padding: 20px;
        width: 293px;
        max-width: 100%;
        
    }
    &__close {
        margin-top: -23px;
        float: right;
        font-size: 28px;
        cursor: pointer;
    }
    &__subtitle {
        text-align: center;
        font-size: 21px;
    }
    &__form {
        &__main {
            text-align: center;
        }
        &__control {
            display: inline-block;
            width: 100%;
            margin-top: 42px;
            border: none;
            border-bottom: 1px solid #685f5f;
        }
    }
    &__btn {
        width: 100%;
        height: 49px;
        margin-top: 48px;
        box-shadow: 0 0 100px 0 rgba(255, 255, 255, 0.11);
        background-color: #00c2f0;
        border-radius: 5%;
        font-family: Roboto;
        font-size: 13px;
        font-stretch: normal;
        font-style: normal;
        letter-spacing: normal;
        text-align: center;
        color: #ffffff;
        &:hover {
            box-shadow: 0 2px 0 #3C93D5;
            transition: 0.5s all;
        } 
    }
}

.fade-in {
    animation: fadeIn ease 1s;
    -webkit-animation: fadeIn ease 1s;
    -moz-animation: fadeIn ease 1s;
    -o-animation: fadeIn ease 1s;
    -ms-animation: fadeIn ease 1s;
}
  
@keyframes fadeIn {
0% {
    opacity: 0;
}
100% {
    opacity: 1;
}
}
@-moz-keyframes fadeIn {
0% {
    opacity: 0;
}
100% {
    opacity: 1;
}
}
@-webkit-keyframes fadeIn {
0% {
    opacity: 0;
}
100% {
    opacity: 1;
}
}
@-o-keyframes fadeIn {
0% {
    opacity: 0;
}
100% {
    opacity: 1;
}
}
@-ms-keyframes fadeIn {
0% {
    opacity: 0;
}
100% {
    opacity: 1;
}
}