@import 'libs/bootstrap-reboot.min.scss';
@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300;900&display=swap');

* {
    font-family: 'Roboto', sans-serif;
    margin: 0;
    padding: 0;
}

body {
    overflow-x: hidden;
}

.container {
    width: 1920px;
    margin: 0 auto;
}

.btn {
    width: 241px;
    height: 60px;
    margin-left: 240px;
    padding: 18px 46px 21px 45px;
    box-shadow: 0 0 100px 0 rgba(255, 255, 255, 0.11);
    background-color: #00c2f0;
    border-radius: 5%;
    font-family: Roboto;
    font-size: 15px;
    font-stretch: normal;
    font-style: normal;
    letter-spacing: normal;
    text-align: left;
    color: #ffffff;
    &:hover {
        box-shadow: 0 2px 0 #3C93D5;
        transition: 0.5s all;
    } 
}

