.advantages {
    &__header {
        margin: 81px 26px 44px 23px;
        font-size: 30px;
        font-weight: 900;
        font-stretch: normal;
        font-style: normal;
        letter-spacing: normal;
        text-align: center;
        color: #393939;
    }
    &__wrapper {
        display: grid;
        grid-template-columns: repeat(2,570px);
        margin-left: 174px;
        gap: 32px;
    }
    &__picture {
        position: relative;
        z-index: -1;
    }
    &__descr {
        position: absolute;
        font-size: 21px;
        font-weight: normal;
        font-stretch: normal;
        font-style: normal;
        letter-spacing: normal;
        text-align: center;
        color: #ffffff;
        margin: -200px 0 0 55px;
        width: 25%;
    }
}