.middle {
    margin-top: 80px;
    height: 890px;
    background: url('../img/bg/bg_middle.jpg') center (center/cover) no-repeat;
    &__header {
        padding-top: 80px;
        font-size: 30px;
        font-weight: 900;
        font-stretch: normal;
        font-style: normal;
        letter-spacing: normal;
        text-align: center;
        color: #ffffff;
    }
    &__menu {
        width: 59%;
        height: 65px;
        display: flex;
        border: 1px solid #ffffffbd;
        justify-content: space-around;
        margin: 25px auto;
        &_link {
            color: #ffffff;
            border-left: 1px solid #ffffffbd;
            padding-left: 25px;
            margin-top: 20px;
            margin-bottom: 18px;
            &:hover {
                color:#00c2f0;
                transition: 0.5s all;
                text-decoration: none;
            }
            &:first-child {
                border-left: none;
            }
        }
    }
    &__notebook {
        width: 947px;
        height: 560px;
        margin: 21px 0px 0 270px;
    }

}    