.variants {
    margin-top: 80px;
    &__header {
        position: absolute;
        margin: 70px 0 0 566px;
        font-size: 30px;
        font-weight: 900;
        font-stretch: normal;
        font-style: normal;
        letter-spacing: normal;
        text-align: center;
        color: #ffffff;
    }
    &__wrapper {
        width: 99%;
        display: flex;
        justify-content: center;
    }
    &__picture {
        width: 798px;
        position: relative;
        z-index: -1;
    }
    &__subheader_left {
        position: absolute;
        margin: -278px 0 0 384px;
        font-size: 30px;
        font-weight: 900;
        font-stretch: normal;
        font-style: normal;
        letter-spacing: normal;
        text-align: right;
        color: #ffffff;
        text-transform: uppercase;
    }
    &__descr_left {
        position: absolute;
        margin: -225px 0 0 264px;
        font-size: 18px;
        font-weight: 300;
        font-stretch: normal;
        font-style: normal;
        letter-spacing: normal;
        text-align: right;
        color: #ffffff;
    }
    &__subheader_right {
        position: absolute;
        margin: -277px 0 0 66px;
        font-size: 30px;
        font-weight: 900;
        font-stretch: normal;
        font-style: normal;
        letter-spacing: normal;
        text-align: right;
        color: #ffffff;
        text-transform: uppercase;
    }
    &__descr_right {
        position: absolute;
        margin: -224px 0 0 67px;
        font-size: 18px;
        font-weight: 300;
        font-stretch: normal;
        font-style: normal;
        letter-spacing: normal;
        text-align: left;
        color: #ffffff;
    }
}