hr {
    width: 1330px;
    margin: 0 auto;
}
.footer {
    height: 650px;
    background-color: #296e96;
    &__wrapper {
        display: grid;
        grid-template-columns: 377px 263px 117px 111px;
        column-gap: 74px;
        margin: 80px 0 0 217px;
        padding-top: 49px;
    }
    &__header {
        font-size: 16px;
        font-weight: bold;
        color: #ffffff;
    }
    &__logo {
        margin: 12px 0 21px 0;
    }
    &__descr {
        font-size: 14px;
        font-weight: 300;
        font-stretch: normal;
        font-style: normal;
        letter-spacing: normal;
        text-align: left;
        color: #ffffff;
    }
    &_link {
        font-size: 13px;
        font-weight: 300;
        font-stretch: normal;
        font-style: normal;
        letter-spacing: normal;
        text-align: left;
        color: #ffffff;
        p {
            height: 2%;
        }
        &:hover {
            transition: 0.5s all;
            color:#00c2f0;
            text-decoration: none;

        }
    }
    &__rights {
        margin: 50px 120px 0 56px;
        font-size: 13px;
        font-weight: 300;
        font-stretch: normal;
        font-style: normal;
        line-height: 1.31;
        letter-spacing: normal;
        text-align: center;
        color: #ffffff;
    }
}