.start {
    &__picture {
        margin: 80px 59px 74px 590px;
    }
    &__header {
        font-size: 30px;
        font-weight: 900;
        font-stretch: normal;
        font-style: normal;
        letter-spacing: normal;
        text-align: center;
        color: #393939;
    }
    &__btn {
        width: 241px;
        height: 60px;
        margin: 30px 0 0 621px;
        padding: 18px 46px 21px 45px;
        box-shadow: 0 0 100px 0 rgba(255, 255, 255, 0.11);
        background-color: #00c2f0;
        border-radius: 5%;
        font-family: Roboto;
        font-size: 15px;
        font-stretch: normal;
        font-style: normal;
        letter-spacing: normal;
        text-align: left;
        color: #ffffff;
        &:hover {
            box-shadow: 0 2px 0 #3C93D5;
            transition: 0.5s all;
        } 
    }
}